'use client';
import { useState, useEffect } from 'react';
import Link from 'next/link';
import { useAuth } from '@/context/AuthContext';
import api from '@/lib/api';

export default function HomePage() {
  const { user } = useAuth();
  const [universities, setUniversities] = useState([]);
  const [hostels, setHostels] = useState([]);
  const [loading, setLoading] = useState(true);
  const [search, setSearch] = useState('');
  const [selectedUniversity, setSelectedUniversity] = useState('');

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const [univRes, hostelRes] = await Promise.all([
        api.get('/hostels/universities'),
        api.get('/hostels/approved')
      ]);
      setUniversities(univRes.data.universities);
      setHostels(hostelRes.data.hostels);
    } catch (error) {
      console.error('Error:', error);
    } finally {
      setLoading(false);
    }
  };

  const filteredHostels = hostels.filter(hostel => {
    const matchesSearch = hostel.name.toLowerCase().includes(search.toLowerCase()) ||
      hostel.city.toLowerCase().includes(search.toLowerCase());
    const matchesUniversity = selectedUniversity
      ? hostel.university_id === selectedUniversity : true;
    return matchesSearch && matchesUniversity;
  });

  return (
    <>
      <style>{`
        @import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@400;600;700&family=DM+Sans:wght@300;400;500;600&display=swap');

        *, *::before, *::after { box-sizing: border-box; margin: 0; padding: 0; }

        :root {
          --ink: #0f1117;
          --ink-light: #3a3d4a;
          --ink-muted: #8a8d9a;
          --cream: #faf9f6;
          --white: #ffffff;
          --gold: #c9a84c;
          --gold-light: #e8d5a0;
          --border: #e4e2dc;
          --shadow: 0 1px 3px rgba(0,0,0,0.06), 0 4px 16px rgba(0,0,0,0.04);
          --shadow-hover: 0 8px 32px rgba(0,0,0,0.12);
        }

        body { background: var(--cream); font-family: 'DM Sans', sans-serif; color: var(--ink); }

        .nav {
          background: var(--white);
          border-bottom: 1px solid var(--border);
          position: sticky; top: 0; z-index: 100;
          padding: 0 40px;
        }
        .nav-inner {
          max-width: 1200px; margin: 0 auto;
          display: flex; align-items: center; justify-content: space-between;
          height: 68px;
        }
        .logo {
          font-family: 'Playfair Display', serif;
          font-size: 22px; font-weight: 700;
          color: var(--ink); text-decoration: none;
          letter-spacing: -0.3px;
          display: flex; align-items: center; gap: 10px;
        }
        .logo-dot { width: 8px; height: 8px; background: var(--gold); border-radius: 50%; }
        .nav-links { display: flex; align-items: center; gap: 8px; }
        .btn-ghost {
          background: none; border: none; padding: 8px 16px;
          font-family: 'DM Sans', sans-serif; font-size: 14px; font-weight: 500;
          color: var(--ink-light); cursor: pointer; text-decoration: none;
          border-radius: 6px; transition: all 0.15s;
        }
        .btn-ghost:hover { color: var(--ink); background: var(--cream); }
        .btn-primary {
          background: var(--ink); color: var(--white);
          border: none; padding: 9px 20px; border-radius: 6px;
          font-family: 'DM Sans', sans-serif; font-size: 14px; font-weight: 500;
          cursor: pointer; text-decoration: none; transition: all 0.15s;
          letter-spacing: 0.1px;
        }
        .btn-primary:hover { background: #1e2230; transform: translateY(-1px); }

        .hero {
          background: var(--ink);
          padding: 100px 40px;
          position: relative; overflow: hidden;
        }
        .hero::before {
          content: '';
          position: absolute; top: 0; right: 0;
          width: 45%; height: 100%;
          background: linear-gradient(135deg, transparent 0%, rgba(201,168,76,0.08) 100%);
          pointer-events: none;
        }
        .hero-grid {
          position: absolute; inset: 0;
          background-image:
            linear-gradient(rgba(255,255,255,0.02) 1px, transparent 1px),
            linear-gradient(90deg, rgba(255,255,255,0.02) 1px, transparent 1px);
          background-size: 60px 60px;
        }
        .hero-inner {
          max-width: 1200px; margin: 0 auto;
          position: relative; z-index: 1;
        }
        .hero-tag {
          display: inline-flex; align-items: center; gap: 8px;
          background: rgba(201,168,76,0.15); border: 1px solid rgba(201,168,76,0.3);
          color: var(--gold-light); padding: 6px 14px; border-radius: 20px;
          font-size: 12px; font-weight: 500; letter-spacing: 0.8px;
          text-transform: uppercase; margin-bottom: 28px;
        }
        .hero h1 {
          font-family: 'Playfair Display', serif;
          font-size: clamp(42px, 6vw, 72px);
          font-weight: 700; color: var(--white);
          line-height: 1.1; letter-spacing: -1px;
          max-width: 700px; margin-bottom: 20px;
        }
        .hero h1 em {
          font-style: italic; color: var(--gold);
        }
        .hero-sub {
          color: rgba(255,255,255,0.55);
          font-size: 17px; font-weight: 300; line-height: 1.7;
          max-width: 500px; margin-bottom: 48px;
        }

        .search-bar {
          background: var(--white);
          border-radius: 10px;
          padding: 6px;
          display: flex; gap: 6px;
          max-width: 680px;
          box-shadow: 0 0 0 1px rgba(255,255,255,0.1);
        }
        .search-bar input, .search-bar select {
          flex: 1; border: none; outline: none;
          padding: 12px 16px; font-size: 14px;
          font-family: 'DM Sans', sans-serif;
          color: var(--ink); background: transparent;
          border-radius: 6px;
        }
        .search-bar select { flex: 0.7; }
        .search-divider {
          width: 1px; background: var(--border);
          margin: 8px 0; flex-shrink: 0;
        }
        .search-btn {
          background: var(--gold); color: var(--ink);
          border: none; padding: 12px 24px; border-radius: 7px;
          font-family: 'DM Sans', sans-serif; font-size: 14px; font-weight: 600;
          cursor: pointer; white-space: nowrap; transition: all 0.15s;
          flex-shrink: 0;
        }
        .search-btn:hover { background: #b8963e; }

        .stats-bar {
          background: var(--white);
          border-bottom: 1px solid var(--border);
          padding: 24px 40px;
        }
        .stats-inner {
          max-width: 1200px; margin: 0 auto;
          display: flex; gap: 48px; align-items: center;
        }
        .stat-item { display: flex; align-items: center; gap: 14px; }
        .stat-icon {
          width: 40px; height: 40px;
          background: var(--cream); border: 1px solid var(--border);
          border-radius: 8px; display: flex; align-items: center; justify-content: center;
          font-size: 18px; flex-shrink: 0;
        }
        .stat-num {
          font-family: 'Playfair Display', serif;
          font-size: 22px; font-weight: 700; color: var(--ink);
          line-height: 1;
        }
        .stat-label { font-size: 12px; color: var(--ink-muted); margin-top: 2px; }
        .stat-divider {
          width: 1px; height: 36px; background: var(--border); flex-shrink: 0;
        }

        .main { max-width: 1200px; margin: 0 auto; padding: 60px 40px; }

        .section-header {
          display: flex; justify-content: space-between; align-items: flex-end;
          margin-bottom: 36px;
        }
        .section-title {
          font-family: 'Playfair Display', serif;
          font-size: 32px; font-weight: 600; color: var(--ink);
          letter-spacing: -0.5px;
        }
        .section-sub { font-size: 14px; color: var(--ink-muted); margin-top: 4px; }
        .view-all {
          font-size: 13px; color: var(--gold); font-weight: 500;
          text-decoration: none; display: flex; align-items: center; gap: 4px;
        }
        .view-all:hover { color: #b8963e; }

        .hostel-grid {
          display: grid;
          grid-template-columns: repeat(auto-fill, minmax(340px, 1fr));
          gap: 24px;
        }
        .hostel-card {
          background: var(--white);
          border: 1px solid var(--border);
          border-radius: 12px; overflow: hidden;
          text-decoration: none; color: inherit;
          transition: all 0.2s; display: block;
          box-shadow: var(--shadow);
        }
        .hostel-card:hover {
          box-shadow: var(--shadow-hover);
          transform: translateY(-3px);
          border-color: var(--gold-light);
        }
        .card-image {
          height: 200px; background: var(--ink);
          display: flex; align-items: center; justify-content: center;
          position: relative; overflow: hidden;
        }
        .card-image-bg {
          position: absolute; inset: 0;
          background: linear-gradient(135deg, #1a1d2e 0%, #2a2d3e 100%);
        }
        .card-image-icon {
          font-size: 48px; position: relative; z-index: 1; opacity: 0.9;
        }
        .card-badge {
          position: absolute; top: 12px; left: 12px; z-index: 2;
          background: rgba(201,168,76,0.9); color: var(--ink);
          font-size: 10px; font-weight: 700; padding: 4px 10px;
          border-radius: 4px; letter-spacing: 0.5px; text-transform: uppercase;
        }
        .card-body { padding: 20px; }
        .card-title {
          font-family: 'Playfair Display', serif;
          font-size: 18px; font-weight: 600; color: var(--ink);
          margin-bottom: 8px; line-height: 1.3;
        }
        .card-location {
          display: flex; align-items: center; gap: 6px;
          font-size: 13px; color: var(--ink-muted); margin-bottom: 4px;
        }
        .card-university {
          font-size: 13px; color: var(--gold);
          font-weight: 500; margin-bottom: 14px;
          display: flex; align-items: center; gap: 6px;
        }
        .card-divider { height: 1px; background: var(--border); margin: 14px 0; }
        .card-footer {
          display: flex; justify-content: space-between; align-items: center;
        }
        .card-price {
          font-size: 13px; color: var(--ink-muted);
        }
        .card-price strong {
          display: block; font-size: 17px; font-weight: 600;
          color: var(--ink); font-family: 'Playfair Display', serif;
        }
        .card-amenities { display: flex; gap: 6px; flex-wrap: wrap; margin-bottom: 14px; }
        .amenity-tag {
          font-size: 11px; padding: 3px 10px;
          background: var(--cream); border: 1px solid var(--border);
          border-radius: 20px; color: var(--ink-light);
          font-weight: 500;
        }
        .btn-card {
          background: var(--ink); color: var(--white);
          border: none; padding: 8px 16px; border-radius: 6px;
          font-family: 'DM Sans', sans-serif; font-size: 13px; font-weight: 500;
          cursor: pointer; transition: all 0.15s; white-space: nowrap;
        }
        .btn-card:hover { background: #1e2230; }

        .empty-state {
          text-align: center; padding: 80px 20px;
          grid-column: 1 / -1;
        }
        .empty-icon {
          width: 72px; height: 72px;
          background: var(--cream); border: 1px solid var(--border);
          border-radius: 16px; display: flex; align-items: center;
          justify-content: center; font-size: 32px;
          margin: 0 auto 20px;
        }
        .empty-title {
          font-family: 'Playfair Display', serif;
          font-size: 22px; color: var(--ink); margin-bottom: 8px;
        }
        .empty-sub { font-size: 14px; color: var(--ink-muted); }

        .footer {
          background: var(--ink); color: var(--white);
          padding: 60px 40px 32px;
          margin-top: 80px;
        }
        .footer-inner {
          max-width: 1200px; margin: 0 auto;
        }
        .footer-top {
          display: grid; grid-template-columns: 1.5fr 1fr 1fr 1fr;
          gap: 48px; padding-bottom: 48px;
          border-bottom: 1px solid rgba(255,255,255,0.08);
        }
        .footer-logo {
          font-family: 'Playfair Display', serif;
          font-size: 20px; font-weight: 700; color: var(--white);
          display: flex; align-items: center; gap: 8px;
          margin-bottom: 14px;
        }
        .footer-desc {
          font-size: 13px; color: rgba(255,255,255,0.4);
          line-height: 1.7; max-width: 240px;
        }
        .footer-col h4 {
          font-size: 11px; font-weight: 600; letter-spacing: 1px;
          text-transform: uppercase; color: rgba(255,255,255,0.35);
          margin-bottom: 16px;
        }
        .footer-col ul { list-style: none; }
        .footer-col ul li { margin-bottom: 10px; }
        .footer-col ul li a {
          font-size: 13px; color: rgba(255,255,255,0.55);
          text-decoration: none; transition: color 0.15s;
        }
        .footer-col ul li a:hover { color: var(--white); }
        .footer-bottom {
          padding-top: 24px;
          display: flex; justify-content: space-between; align-items: center;
        }
        .footer-copy { font-size: 12px; color: rgba(255,255,255,0.25); }
        .footer-gold { color: var(--gold); }
      `}</style>

      {/* NAV */}
      <nav className="nav">
        <div className="nav-inner">
          <Link href="/" className="logo">
            <span className="logo-dot"></span>
            DormLink
          </Link>
          <div className="nav-links">
            {user ? (
              <Link href={`/${user.role}/dashboard`} className="btn-primary">
                Dashboard
              </Link>
            ) : (
              <>
                <Link href="/login" className="btn-ghost">Sign in</Link>
                <Link href="/register" className="btn-primary">Get Started</Link>
              </>
            )}
          </div>
        </div>
      </nav>

      {/* HERO */}
      <section className="hero">
        <div className="hero-grid"></div>
        <div className="hero-inner">
          <div className="hero-tag">
            <span>‚óè</span> Tanzania&apos;s #1 Student Housing Platform
          </div>
          <h1>
            Find Your <em>Perfect</em><br />Student Home
          </h1>
          <p className="hero-sub">
            Verified accommodation near top Tanzanian universities.
            Safe, affordable, and booked in minutes.
          </p>

          <div className="search-bar">
            <input
              type="text"
              placeholder="Search by hostel name or city..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
            />
            <div className="search-divider"></div>
            <select
              value={selectedUniversity}
              onChange={(e) => setSelectedUniversity(e.target.value)}
            >
              <option value="">All Universities</option>
              {universities.map(u => (
                <option key={u.id} value={u.id}>{u.name}</option>
              ))}
            </select>
            <button className="search-btn">Search</button>
          </div>
        </div>
      </section>

      {/* STATS */}
      <div className="stats-bar">
        <div className="stats-inner">
          <div className="stat-item">
            <div className="stat-icon">üè¢</div>
            <div>
              <div className="stat-num">{hostels.length}+</div>
              <div className="stat-label">Verified Properties</div>
            </div>
          </div>
          <div className="stat-divider"></div>
          <div className="stat-item">
            <div className="stat-icon">üéì</div>
            <div>
              <div className="stat-num">{universities.length}</div>
              <div className="stat-label">Universities Covered</div>
            </div>
          </div>
          <div className="stat-divider"></div>
          <div className="stat-item">
            <div className="stat-icon">‚úÖ</div>
            <div>
              <div className="stat-num">100%</div>
              <div className="stat-label">Verified Listings</div>
            </div>
          </div>
          <div className="stat-divider"></div>
          <div className="stat-item">
            <div className="stat-icon">üîí</div>
            <div>
              <div className="stat-num">Secure</div>
              <div className="stat-label">Payments Protected</div>
            </div>
          </div>
        </div>
      </div>

      {/* LISTINGS */}
      <main className="main">
        <div className="section-header">
          <div>
            <h2 className="section-title">Available Properties</h2>
            <p className="section-sub">
              {loading ? 'Loading...' : `${filteredHostels.length} properties found`}
            </p>
          </div>
          <Link href="/hostels" className="view-all">
            View all ‚Üí
          </Link>
        </div>

        <div className="hostel-grid">
          {loading ? (
            [1,2,3].map(i => (
              <div key={i} style={{
                background: 'var(--white)', border: '1px solid var(--border)',
                borderRadius: '12px', height: '360px',
                animation: 'pulse 1.5s infinite'
              }}></div>
            ))
          ) : filteredHostels.length === 0 ? (
            <div className="empty-state">
              <div className="empty-icon">üè†</div>
              <h3 className="empty-title">No properties found</h3>
              <p className="empty-sub">Try adjusting your search or check back soon</p>
            </div>
          ) : (
            filteredHostels.map(hostel => (
              <Link key={hostel.id} href={`/hostels/${hostel.id}`} className="hostel-card">
                <div className="card-image">
                  <div className="card-image-bg"></div>
                  <div className="card-image-icon">üè¢</div>
                  <div className="card-badge">Verified</div>
                </div>
                <div className="card-body">
                  <h3 className="card-title">{hostel.name}</h3>
                  <div className="card-location">
                    <span>üìç</span> {hostel.address}, {hostel.city}
                  </div>
                  {hostel.universities && (
                    <div className="card-university">
                      <span>üéì</span> {hostel.universities.name}
                    </div>
                  )}
                  {hostel.amenities && hostel.amenities.length > 0 && (
                    <div className="card-amenities">
                      {hostel.amenities.slice(0, 3).map((a, i) => (
                        <span key={i} className="amenity-tag">{a}</span>
                      ))}
                    </div>
                  )}
                  <div className="card-divider"></div>
                  <div className="card-footer">
                    <div className="card-price">
                      <strong>
                        {hostel.rooms && hostel.rooms.length > 0
                          ? `TZS ${Math.min(...hostel.rooms.map(r => r.price_per_month)).toLocaleString()}`
                          : 'Contact for price'
                        }
                      </strong>
                      per month
                    </div>
                    <button className="btn-card">View Details ‚Üí</button>
                  </div>
                </div>
              </Link>
            ))
          )}
        </div>
      </main>

      {/* FOOTER */}
      <footer className="footer">
        <div className="footer-inner">
          <div className="footer-top">
            <div>
              <div className="footer-logo">
                <span style={{width:'8px',height:'8px',background:'var(--gold)',borderRadius:'50%',display:'inline-block'}}></span>
                DormLink
              </div>
              <p className="footer-desc">
                Tanzania&apos;s trusted platform for verified student accommodation near top universities.
              </p>
            </div>
            <div className="footer-col">
              <h4>Platform</h4>
              <ul>
                <li><Link href="/register">Register</Link></li>
                <li><Link href="/login">Sign In</Link></li>
                <li><Link href="/hostels">Browse Hostels</Link></li>
              </ul>
            </div>
            <div className="footer-col">
              <h4>Hosts</h4>
              <ul>
                <li><Link href="/register">List Property</Link></li>
                <li><Link href="/login">Host Dashboard</Link></li>
              </ul>
            </div>
            <div className="footer-col">
              <h4>Contact</h4>
              <ul>
                <li><a href="mailto:support@dormlink.co.tz">support@dormlink.co.tz</a></li>
                <li><a href="#">Help Center</a></li>
              </ul>
            </div>
          </div>
          <div className="footer-bottom">
            <p className="footer-copy">¬© 2024 <span className="footer-gold">DormLink</span>. All rights reserved.</p>
            <p className="footer-copy">Built for Tanzanian students.</p>
          </div>
        </div>
      </footer>
    </>
  );
}